import api from '../../common/request.js'
const app = getApp()
import { mapMutations } from 'vuex'
	
export default {
	data() {
		return {
			userInfo: {},
			screenHeight: 0,
			imgUrl: 'http://m.haokanread.com/static/default-head.png',
			// canIUse: uni.canIUse('button.open-type.getUserInfo'),
			hasUserInfo: false
		}
	},
	onLoad() {
		this.userInfo = uni.getStorageSync('__user__') || {}
		if (this.userInfo.avatarUrl != '') {
			this.imgUrl = this.userInfo.avatarUrl
		}
	},
	onReady() {
		let screenHeight = uni.getSystemInfoSync().windowHeight
		this.screenHeight = screenHeight * 2
	},
	methods: {
		...mapMutations(['setToken']),
		getUserInfo() {
			var provider = ''
			var para = {}
			uni.getProvider({
				service: 'oauth',
				success: (res) => {
					provider = res.provider
					// console.log(provider)
					// alipay
					if (~res.provider.indexOf('alipay')) {
						uni.login({
							provider,
							scopes: 'auth_user',
							success: res => {
								// console.log(res)
								para.code = res.code
								uni.getUserInfo({
									provider,
									lang: 'zh_CN',
									success: infoRes => {
										// console.log(infoRes)
										para.nickname = infoRes.userInfo.nickName
										api.post('/mini_login/aliPayLogin', para).then(res => {
											if (res.code === 200) {
												this.userInfo.userId = res.data.user_id
												this.userInfo.userName = res.data.user_name
												this.userInfo.apiToken = res.data.api_token
												this.userInfo.nickName = infoRes.userInfo.nickName
												this.userInfo.avatarUrl = infoRes.userInfo.avatarUrl
												this.imgUrl = infoRes.userInfo.avatarUrl
												this.setToken(res.data)
												uni.setStorageSync('__user__', this.userInfo)
											} else {
												uni.showToast({
												  title: res.msg,
												  icon: 'none',
												  duration: 2000
												})
											}
										})
									}
								})
							}
						})
					} else {
						
					}
					
				}
			})
		},
		copyUserName() {
		  let account = this.userInfo.nickName
		  uni.setClipboardData({
		    data: account,
		    success: ()=>{
		      uni.showToast({
		        title: '复制成功',
		        duration: 2000
		      })
		    }
		  })
		}
		
	},
	onTabItemTap(data) {
		// console.log(data)
	}
}	
